import React, { Component } from 'react';
import './App.css';

import Game from './components/game/Game';

export default class App extends Component {
  constructor() {
    super();

    this.state = {
      score: 0,
    };
  }

  updateScore(burstedCandies) {
    this.setState({
      score: this.state.score + burstedCandies,
    });
  }

  render() {
    return (
      <div className="App">
        <h1>Score: {this.state.score}</h1>
        <Game onBurstCandies={this.updateScore.bind(this)} />
      </div>
    );
  }
}
